<script>
  import { getters, contract as currentContract, changeContract, poolMenu } from '../../contract'
  import { onboard, changeWallets } from '../../init'

  export default {
    metaInfo: {
      title: 'Swerve.fi',
      meta: [
        {'property': 'og:title', 'content': 'swerve.fi'},
        {'property': 'og:url', 'content': 'https://swerve.fi'},
        {'property': 'og:type', 'content': 'website'},
        {'property': 'og:description', 'content': 'Swerve is an exchange liquidity pool on Ethereum designed for extremely efficient stablecoin trading'},
        {'property': 'og:image', 'content': '/swerve.png'},
      ]
    },
    components: {

    },
    computed: {
      ...getters,
      poolMenu() {
        return poolMenu;
      },
      publicPath() {
        return process.env.BASE_URL
      },
      logoSrc() {
        if(!currentContract.swapbtc) return this.publicPath + 'logo_optimized.svg'
        else return this.publicPath + 'logo_ren_beta_optimized.svg'
      },
      hasConnectedWallet() {
        return this.default_account == '0x0000000000000000000000000000000000000000' 
                && !['StatsDaily', 'Audits', 'Stats', 'Contracts'].includes(this.$route.name)
      },
      plsReturn() {
        return currentContract.currentContract.toLowerCase() == '0x72c20f89008729c91b6bb85f3104fda942494cef'.toLowerCase()
      },
    },
    methods: {
      changePools(pool) {
        changeContract(pool)
      },
      async changeWallets() {
        changeWallets()
      },
      async changeAccounts() {
        return onboard.accountSelect();
      },
    },
  }
</script>

